.friend__section {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.item {
    border: 1px solid #EBEFF2;
    display: flex;
    min-width: 250px;
    padding-top: 5px;
    padding-bottom: 10px;
    height: auto;
    margin-bottom: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}

.item:last-child {
    margin-bottom: 0;
}
.item:hover{
    border: 1px solid #c3c7ca;
}

.statusItem {
    margin-bottom: auto;
    margin-top: auto;
    margin-left: 10px;
    margin-right: 10px;
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
}

.statusItem.isOnline {
    background-color: green;
}

.avatar {
    margin-right: 10px;
}

.name {
    margin-top: auto;
    margin-bottom: auto;
    font-size: 25px;

}